@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmonoregular.woff2") format("woff2"), url("../fonts/ptmonoregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedregular.woff2") format("woff2"), url("../fonts/robotocondensedregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Courier", monospace;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  text-decoration: none;
}
a:focus {
  outline: 2px solid #4c97b7;
}
a:hover, a:focus {
  opacity: 0.8;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #f2f5f6;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 85%;
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #f2f5f6;
  text-transform: uppercase;
  cursor: pointer;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  border-radius: 3px;
  border: none;
  user-select: none;
}
.btn:focus {
  outline: 2px solid #4c97b7;
}
.btn:hover, .btn:focus {
  opacity: 0.8;
}
.btn:disabled {
  opacity: 0.5;
}

.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding-top: 30px;
  padding-bottom: 35px;
  position: sticky;
  top: 0;
  z-index: 5;
}
@media (max-width: 1023px) {
  .header {
    position: static;
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.header__logo {
  display: flex;
  align-self: start;
}
.header__logo svg {
  display: block;
}
.header__logo:focus {
  outline: 2px solid #4c97b7;
}
@media (max-width: 767px) {
  .header__logo {
    width: 35px;
    height: 35px;
    align-self: center;
  }
}

.header__navigation {
  display: flex;
  padding-right: 3%;
  justify-content: space-between;
  align-items: start;
}
@media (max-width: 1023px) {
  .header__navigation {
    padding-right: 70px;
  }
}
@media (max-width: 767px) {
  .header__navigation {
    padding-right: 35px;
  }
}

.header__contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-width: 43%;
  max-width: 35%;
  margin-top: 12px;
}
.header__contacts svg {
  margin-right: 15px;
  flex-shrink: 0;
}
@media (max-width: 1023px) {
  .header__contacts {
    display: flex;
    justify-content: end;
    margin: 0;
    max-width: 50%;
    padding: 0;
    align-self: start;
    flex-wrap: wrap;
  }
  .header__contacts svg {
    margin-right: 0;
  }
}

.header__link {
  display: flex;
  align-items: center;
  margin-left: 16px;
  color: #f2f5f6;
  width: fit-content;
}
@media (max-width: 1023px) {
  .header__link {
    width: 40px;
    height: 40px;
    justify-content: center;
    margin-left: 20px;
  }
}
@media (max-width: 767px) {
  .header__link {
    align-items: center;
  }
}

@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}

.header__button {
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #041827;
  border: 1px solid #f2f5f6;
  cursor: pointer;
  padding: 10px 15px;
  max-width: 160px;
  text-align: center;
  margin-right: 4px;
  margin-top: 6px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}
.header__button:hover, .header__button:focus {
  opacity: 0.8;
}

.intro {
  background-image: url("../img/background-desktop.jpg"), url("../img/background-desktop.webp");
  width: 100%;
  position: relative;
  color: #f2f5f6;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  min-height: 880px;
  margin-top: -120px;
  background-repeat: no-repeat;
  background-size: cover;
}
.intro::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
@media (min-resolution: 2dppx) {
  .intro {
    background-image: url("../img/background-desktop@2x.jpg"), url("../img/background-desktop@2x.webp");
    width: 100%;
  }
}
@media (max-width: 1023px) {
  .intro {
    background-position: 20%;
    background-repeat: no-repeat;
    min-height: 640px;
    margin-top: -100px;
  }
}
@media (max-width: 767px) {
  .intro {
    background-position: 25%;
    background-repeat: no-repeat;
    margin-top: -70px;
    min-height: 560px;
  }
}

.intro__inner {
  position: relative;
  z-index: 1;
  padding-top: 305px;
  padding-left: 125px;
  padding-bottom: 280px;
}
@media (max-width: 1023px) {
  .intro__inner {
    padding-top: 180px;
    padding-left: 50px;
    padding-bottom: 205px;
  }
}
@media (max-width: 767px) {
  .intro__inner {
    padding-top: 150px;
    padding-left: 25px;
    padding-bottom: 190px;
  }
}
.intro__inner h1 {
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 32px;
}
@media (max-width: 1023px) {
  .intro__inner h1 {
    font-size: 64px;
    line-height: 75px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .intro__inner h1 {
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 8px;
  }
}
.intro__inner p {
  font-family: "PT Mono", "Courier", monospace;
  font-size: 22px;
  margin-top: 0;
  margin-bottom: 41px;
  line-height: 36px;
  max-width: 670px;
}
@media (max-width: 1023px) {
  .intro__inner p {
    line-height: 34px;
    margin-bottom: 57px;
  }
}
@media (max-width: 767px) {
  .intro__inner p {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 44px;
  }
}

.intro__button {
  min-height: 55px;
  width: 410px;
  font-size: 18px;
  line-height: 21px;
  padding: 16px 25px 17px;
  background-color: #00b2ff;
  overflow: hidden;
}
@media (max-width: 1023px) {
  .intro__button {
    margin-left: 46px;
  }
}
@media (max-width: 767px) {
  .intro__button {
    justify-content: center;
    height: 40px;
    width: 100%;
    padding: 10px 30px;
    font-size: 14px;
    line-height: 16px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .intro__button span:first-child {
    display: none;
  }
}
.intro__button span:last-child {
  display: none;
}
@media (max-width: 767px) {
  .intro__button span:last-child {
    display: block;
  }
}

.advantages {
  padding-top: 120px;
  padding-bottom: 70px;
}
@media (max-width: 1023px) {
  .advantages {
    padding-top: 80px;
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 50px;
  }
}

@media (max-width: 1023px) {
  .advantages.container {
    padding: 79px 54px 40px 46px;
  }
}
@media (max-width: 767px) {
  .advantages.container {
    padding: 50px 26px 0 24px;
  }
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: minmax(300px, auto);
  gap: 30px;
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  border: 1px solid #ebebeb;
  position: relative;
  padding: 46px 44px;
  min-height: 298px;
}
.advantages__item::before {
  content: "";
  width: 5px;
  background-color: #00b2ff;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
}
.advantages__item svg {
  color: #00b2ff;
  display: block;
}
.advantages__item h3 {
  margin: 0;
  font-size: 24px;
  line-height: 35px;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #00b2ff;
  align-self: center;
  margin-top: 15px;
  padding: 0;
}
.advantages__item p {
  font-size: 16px;
  line-height: 22px;
  margin: 0;
  overflow: auto;
  font-weight: 400;
  font-family: "PT Mono", "Courier", monospace;
  color: #041827;
  margin-top: 10px;
}
.advantages__item:hover {
  background-color: #00b2ff;
  color: #f2f5f6;
}
.advantages__item:hover svg {
  color: #f2f5f6;
}
.advantages__item:hover p {
  color: #f2f5f6;
}
.advantages__item:hover h3 {
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .advantages__item {
    padding-bottom: 28px;
    padding-right: 30px;
    padding-top: 38px;
    min-height: 360px;
  }
}
@media (max-width: 767px) {
  .advantages__item {
    padding: 30px 30px 35px;
    min-height: 340px;
  }
  .advantages__item svg {
    width: 30px;
    height: 30px;
  }
  .advantages__item h3 {
    font-size: 20px;
    line-height: 28px;
    padding: 0;
  }
  .advantages__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

@media (max-width: 767px) {
  .advantages__item--small {
    padding: 28px 31px 35px 28px;
    min-height: 325px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--small svg {
    margin-bottom: 4px;
  }
}
@media (max-width: 767px) {
  .advantages__item--small svg {
    margin-bottom: 8px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--small h3 {
    margin-top: 11px;
  }
}
@media (max-width: 767px) {
  .advantages__item--small h3 {
    margin-top: 18px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--small p {
    margin-top: 4px;
  }
}
@media (max-width: 767px) {
  .advantages__item--small p {
    margin-top: 15px;
  }
}

.company {
  padding-top: 50px;
  padding-bottom: 100px;
