}
@media (max-width: 1023px) {
  .company__inner {
    margin-left: 29px;
  }
}
@media (max-width: 767px) {
  .company__inner {
    margin: 0;
  }
}
.company__inner h3 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 12px;
}
@media (max-width: 1023px) {
  .company__inner h3 {
    margin-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .company__inner h3 {
    font-size: 28px;
