.company__description {
  height: 170px;
  overflow: hidden;
  margin-bottom: 25px;
}
.company__description-opened {
  height: auto;
}
@media (max-width: 1023px) {
  .company__description {
    height: 220px;
    margin-bottom: 8px;
  }
  .company__description-opened {
    height: auto;
  }
}
@media (max-width: 767px) {
  .company__description {
    height: 211px;
