  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 36px 40px 45px;
  margin: auto;
  background-color: #00b2ff;
  border-radius: 3px;
}
.modal__close-button {
  position: absolute;
  top: 16px;
  right: 16px;
  z-index: 1;
  width: 20px;
  height: 20px;
  padding: 20px;
  background-color: transparent;
  background-image: url("../img/svg/close-button.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: #ffffff;
  margin: 0 0 4px;
}

.modal__form p {
  font-family: "PT Mono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0 0 38px;
}

